.ticket {
  flex-grow: 1;
  display: flex;
  margin-bottom: 30px;
  border: 2px solid #e4efff;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 3px 6px 12px 0px #929bff1c;
  font-size: 18px;
}

.ticket__left {
  padding: 30px;
  min-width: 207px;
  border-right: 1px solid #e4e7e8;
}

.ticket__right {
  /* flex-grow: 1; */
  flex: 1 1 100%;
}

@media (max-width: 1130px) {
  .ticket {
    flex-wrap: wrap;
  }

  .ticket__left {
    width: 100%;
  }
}