.ticket__place {
  flex-grow: 1;
}

.ticket__place:last-child {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.ticket__time {
  font-size: 48px;
  padding-bottom: 10px;
}

.ticket__city {
  font-weight: 600;
  line-height: 25px;
}

.ticket__date {
  color: #9ea9b7;
}